@import 'src/assets/styles/variables.scss';
@import 'src/assets/styles/mixins.scss';

.wrapper {
  width: 140rem;
  height: 84.7rem;
  position: fixed;
  top: 5%;
  left: 12%;

  z-index: 210;
  display: flex;

  @media (max-width: $break-point-mobile) {
    display: none;
  }

  .pop_up {
    padding: 3rem 3rem;
    background-color: $main-white-color;
    box-shadow: $project-box-shadow;
    width: 140rem;
    height: 84.7rem;
    box-sizing: border-box;
    margin: auto;
    z-index: 12;
    display: flex;
    flex-direction: column;
    align-items: center;

    .popUpNameWrapper {
      .popUpName {
        font-size: 2.8rem;
      }
    }

    .popUpInputWrapper {
      height: 15rem;
      display: flex;
      align-items: flex-end;
      .popUpInput {
        width: 44rem;
        border: none;
        border-bottom: 0.3rem solid #c2c2c2;
        outline: none;
      }
    }

    .upperBlock {
      display: flex;
      justify-content: space-between;
      align-items: center;
      margin-bottom: 4rem;
      width: 100%;

      .cross {
        width: 3.2rem;
        height: 3.2rem;
        cursor: pointer;

        :nth-child(1),
        :nth-child(2) {
          width: 3.2rem;
          height: 0;
          border: 0.1rem solid black;
          position: absolute;
        }
        :nth-child(1) {
          transform: rotate(45deg);
        }
        :nth-child(2) {
          transform: rotate(-45deg);
        }
      }

      img {
        width: 5.2rem;
        height: 5.2rem;
      }

      .name {
        font-size: 4rem;
        font-weight: bold;
        margin: 0;
        position: absolute;

        position: absolute;

        transform: rotate(-90deg);
        text-transform: uppercase;
        left: -10rem;
        bottom: 40.5rem;
      }
    }
    .dataBlock {
      display: flex;
      align-items: center;
      margin-top: 3rem;

      .cashIcon {
        width: 3.1rem;
        height: 2.8rem;
        margin-right: 3rem;
      }

      .cashIconBig {
        width: 6.2rem;
        height: 5.7rem;
      }

      .leftPart,
      .rightPart {
        display: flex;
        flex-direction: column;
        width: 21rem;

        .title,
        .data {
          font-size: 1.8rem;
          white-space: pre-line;
        }

        .title {
          font-weight: bold;
          margin: 0;
        }

        .data {
          font-weight: 400;
          margin-top: 0.5rem;
        }

        .titleBig,
        .dataGreen {
          font-size: 4.4rem;
          white-space: pre-line;
          margin-left: 3rem;
          text-align: left;
          width: 40rem;
        }

        .titleBig {
          font-weight: bold;
          margin: 0;
          line-height: 6.5rem;
        }

        .dataGreen {
          font-weight: 200;
          margin-top: 0;
          color: $main-green-color;
          margin-left: 0;
        }
      }
    }

    .buttonBlock {
      height: 30rem;
      display: flex;
      align-items: flex-end;
      .button {
        cursor: pointer;
        font-size: 2rem;
        font-weight: bold;
        text-transform: uppercase;
        text-align: center;

        width: 26.6rem;
        height: 5.7rem;
        background-color: $main-green-color;

        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 2.5rem;
        color: white;
      }

      .disabled {
        font-size: 2rem;
        font-weight: bold;
        text-transform: uppercase;
        text-align: center;

        width: 26.6rem;
        height: 5.7rem;
        background-color: #d3d7d8;

        display: flex;
        align-items: center;
        justify-content: center;
        margin-top: 2.5rem;
        color: white;
      }
    }
  }
}

.popUpNameWrapperPending {
  width: 50rem;
  margin-top: 10rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;

  a {
    text-decoration: none;
  }
  .roundedButton {
    width: 20rem;
    height: 4rem;
    background-color: $main-green-color;
    border-radius: 3rem;
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    cursor: pointer;

    .arrow {
      width: 2rem;
      height: 2rem;
    }

    p {
      color: white;
      font-size: 1.5rem;
      margin: 0;
    }
  }
}

.pendingPopText {
  display: flex;
  flex-direction: column;
  align-items: center;
  p {
    text-align: center;
  }
}
