@import 'src/assets/styles/variables.scss';
@import 'src/assets/styles/mixins.scss';

.wrapper {
  padding: $project-basic-padding;
  padding-top: 5rem;
  display: flex;
  flex-direction: column;

  .connectNotification {
    width: 140rem;
    height: 30rem;
    box-shadow: $project-box-shadow;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;

    .icon {
      width: 9.5rem;
      height: 9.9rem;
    }

    .text {
      font-size: 2.8rem;
      font-weight: bold;
      margin-left: 5rem;
    }
  }
}

.overlay {
  width: 100vw;
  height: 100vh;

  position: fixed;
  background: rgba(0, 0, 0, 0.4);
  z-index: 210;
  display: flex;
}
